body{
    margin:0;
    padding:0;
    height:100vh;
    display:flex;
justify-content: center;
align-items: center;
background:skyblue;
}
.holder{
    width:400px;
    height:500px;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction:column;
    background:transparent;
    gap:1em;
    box-shadow:0px 0px 6px;
}
.text{
    display:flex;
    align-items: center;
    gap:1em;
}
input[type=text]{
    border:2px solid green;
    width:200px;
    outline:none;
    border-radius:6px;
    font-family:"hammersmith one";
    padding:0.6em;
}
button{
    font-family:"hammersmith one";
font-size:1.4rem;
border:1px solid black;
outline:none;
border-radius:50%;
transition: 0.3s ease;
box-shadow:0px 0px 5px;
}
button:hover{
    background:lightgreen;
}
.lists{
    display:flex;
    align-items: center;
    width:280px;
    height: 40px;
    

 

   
    justify-content: space-between;
}
ol li{
list-style: none;
}